.bingoBoard {
    background-color: #000000;
    font-size: max(5vw, 2.5rem);
    font-weight: bold;
    width: 65vw;
    padding: 18px 36px 36px 36px;
}

.bingoLabels {
    display: grid;
    grid-template-columns: repeat(5, 1fr);
    justify-items: stretch;
    gap: 3vw;
}

.bingoElements {
    display: grid;
    grid-template-rows: repeat(5, 1fr);
    justify-items: stretch;
    grid-auto-flow: column;
    gap: 3vw;
}
